@import '../../mixins.scss';
.contact-form{
    padding: 0 3rem;
    display: flex;
    height: 100%;
    margin-top: 4rem;
    @include sm{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0 2px;
        gap: 1rem;
    }
}
.c-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
    
}
.c-right>form{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    
}
.c-right .user{
    width: 20rem;
    height: 2rem;
    padding: 0.3rem;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 8px;
    font-size: 16px;
    @include sm{
        width: 130%;
    }
}
textarea{
    height: 4rem !important;
}
.c-blur1{
    top: 1rem;
    left: 8rem;
    
}